.post {

    /*display: flex;*/
    justify-content: space-between;

    padding: 5px;
    border: 1px solid steelblue;
    margin-top: 10px;
    /*align-items: center;*/
    background: lightblue;

    overflow: hidden;
    /*transition: 3000ms;*/
    /*align-items: center;*/

}

.post input[type="checkbox"] {

}


.post-enter {

  transform: translateX(350px);
}

.post-enter-active {

  transform: translateX(0px);
  transition: all 500ms ease-in;
}

.post-exit {

  opacity: 1;
}

.post-exit-active {

  transform: translateX(-350px);
  transition: all 500ms ease-in;
}



.post__content {
    display: inline-block;
    /*overflow: hidden;*/
